
import tabGroup from '@/components/group/tab'
import bigAnniu from '@/components/group/biganniu'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
components: {
tabGroup: tabGroup,
bigAnniu: bigAnniu,
XInput,
Group,
Grid,
GridItem
},
data () {
return {
anniu: '登录',
list: [
{ label: '微信', src: '微信', router: 'weixinlogin' },
{ label: 'QQ', src: 'qq', router: '' },
{ label: '微博', src: '微博', router: '' },
{ label: '短信登录', src: '短信', router: '' }
],
phone: '',
verCod: ''
}
},
props: {},
computed: {},
methods: {
geticon (a) {
return require('./img/' + a + '.png')
},
empower (a) {
this.$router.push('/' + a)
},
login () {
this.$router.push('/login')
},
register () {
this.$router.push('/register')
},
changepassword () {
this.$router.push('/changepassword')
},
success () {
this.$http
.post('sumuji-server/user/acLogin', {
userName: this.phone,
verCod: this.verCod
})
.then(res => {
if (res.data.data === true) {
this.phone = ''
this.verCod = ''
this.$router.push({
path: '/',
query: { from: this.$route.query.index }
})
}
})
},
clear () {
this.phone = ''
}
}
}



